import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { RouterModule } from '@angular/router';
import { CheckResultComponent } from './checkresult.component';
import { CheckResultListComponent } from './checkresultlist/checkresultlist.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    RouterModule.forChild([
      {
        path: '',
        component: CheckResultComponent
      },
      {
        path: 'list',
        component: CheckResultListComponent
      }
    ])
  ],
  declarations: [CheckResultComponent, CheckResultListComponent]
})
export class CheckResultComponentModule {}
