import { Component, OnInit } from '@angular/core';
//import { resetComponentState } from '@angular/core/src/render3/instructions';
import { CommonService } from '../services/common.service';
import { Mark6Service } from '../services/mark6.service';
import { Router, ActivatedRoute } from '@angular/router';
import { NavController, LoadingController } from '@ionic/angular';
import { DataVal } from '../models/DataVal';
import { MarkSixResult } from '../models/common.model';

@Component({
  selector: 'app-checkresult',
  templateUrl: './checkresult.component.html',
  styleUrls: ['./checkresult.component.scss']
})
export class CheckResultComponent implements OnInit {

  rows = [0, 1, 2, 3, 4, 5, 6];
  cols = [0, 1, 2, 3, 4, 5, 6];

  balls = [
    false, false, false, false, false, false, false, false, false, false, // 10
    false, false, false, false, false, false, false, false, false, false, // 20
    false, false, false, false, false, false, false, false, false, false, // 30
    false, false, false, false, false, false, false, false, false, false, // 40
    false, false, false, false, false, false, false, false, false, // 49
  ];

  constructor(private commonService: CommonService, private mark6Service: Mark6Service,
    private _router: Router, private _activeRoute: ActivatedRoute,
    private navCtrl: NavController, private loadingController: LoadingController,
    private dataVal: DataVal) {
  }

  ngOnInit() {
    this.commonService.prepareRewardVideoAds();
  }

  ionViewDidEnter() {
  }

  reset() {

    for (let n = 0; n < 49; n++) {
      this.balls[n] = false;
    }

  }

toggle(num: number) {
  if (this.balls[num] === true) {
    this.balls[num] = false;
  } else {
    this.balls[num] = true;
  }
}

  async check() {

    let selectedNumber = new Array<number>();

    for (let n = 0; n < this.balls.length; n++) {
      if (this.balls[n]) {
        const theSelectedNo = n + 1;
        selectedNumber.push(theSelectedNo);
      }
    }

    if (selectedNumber.length < 6) {
      this.commonService.showAlert('Error', '最少選擇6個字');
      return;
    }

    // this.commonService.showAlert('Warning', '此功能尚未完成');

    this.commonService.showRewardVideoAds();


    let loading = await this.loadingController.create({
      message: '核對中...',
      spinner: 'circles'
    });

    loading.present().then(() => {
      this.mark6Service.getLast30().subscribe(result => {

        let matchedResults = new Array<MarkSixResult>();

        if (result != null && result.length > 0) {
          for (let iResult of result) {
            let matchCount = 0;

            for (const selNo of selectedNumber) {
              if (selNo === iResult.number1) {
                iResult.matched[0] = true;

                matchCount++;
              } else if (selNo === iResult.number2) {
                iResult.matched[1] = true;

                matchCount++;
              } else if (selNo === iResult.number3) {
                iResult.matched[2] = true;

                matchCount++;
              } else if (selNo === iResult.number4) {
                iResult.matched[3] = true;

                matchCount++;
              } else if (selNo === iResult.number5) {
                iResult.matched[4] = true;

                matchCount++;
              } else if (selNo === iResult.number6) {
                iResult.matched[5] = true;

                matchCount++;
              }

              if (selNo === iResult.numberX) {
                iResult.matched[6] = true;
                matchCount = matchCount + 0.5;
              }

            }

            if (matchCount >= 3) {
              matchedResults.push(iResult);
            }

          }

          loading.dismiss();

          if (matchedResults.length >= 1) {
            this.dataVal.storage = matchedResults;
            this.navCtrl.navigateForward(['checkresult/list']);
          } else {
            this.commonService.showAlert('Info', '閣下輸入的號碼在最近30期沒有中獎');
          }
        } else {
          loading.dismiss();
        }
      }, error => {
        loading.dismiss();
        console.log(error);
      });
    });

  }

}
